import Home from '../page/home/Home'
import History from '../page/history/History'


interface router{
    path:string,
    component:any,
    children?:Array<router>
}

const routers:Array<router> = [
    {
        path:'/',
        component:Home
    },
    {
        path:'/history',
        component:History
    }
]

export default routers;