import React,{useState,useEffect} from 'react';
import { useTranslation } from 'react-i18next';
import './Langage.scss'
import { Select } from 'antd';
const { Option } = Select;
// import lang from './lang'

const Langage = ()=>{
    const { t, i18n } = useTranslation();
    const handleChange = (value:string) =>{
        localStorage.setItem('lang', value);
        i18n.changeLanguage(value);
    }

    return(
        <div className="langage">
            <Select defaultValue={localStorage.getItem('lang') == 'zh_CN' ? window.t('chinese') : window.t('english')} style={{ width: 120 }} onChange={handleChange}>
                <Option value="zh_CN">{window.t('chinese')}</Option>
                <Option value="EN">{window.t('english')}</Option>
            </Select>
        </div>
    )
}

export default Langage;