/*eslint-disable*/
import { Component } from 'react';
import { Row, Col, message } from 'antd';
import './History.scss';
import { ERC20Contract, BridgeCenterContract } from 'sdk/bin/config/config';
import {
    messageWay,
    sdkWay,
    callWeb3,
    sendWeb3,
    getSessionStorage,
    MAIN_ADDRESS
} from 'utils/utils';
import { store } from 'store/index'

const sdk = sdkWay();

var contractToken:any;
var publicChainArr:any[] = []; //

export default class HashrateDetail extends Component<any, any> {
    constructor(props:any) {
        super(props);

        this.state = {
            detailList: [],
            storeAddress:window.$address,
            storeisMobile:window.isMobile,
            chainList: [],
            showUser: false,
            userAddress: '',
            isMore:false,
            isDisetMore:false,
            identifierList: [
                {
                    img: 'assets/images/m-bianBig.png',
                    text: 'Binance Smart Chain Network',
                    name: 'BSC',
                    index: 1
                },
                {
                    img: 'assets/images/halo@2x.png',
                    text: 'DT Network',
                    name: 'DT',
                    index: 2
                }
            ]
        };
    }

    componentDidMount = async () => {
        this.init();
    };

    init = async () => {
        let getSissList = getSessionStorage("chainList");
        if(!getSissList) return;
        publicChainArr = JSON.parse(getSissList);
        // console.log(this.props)
        let index = this.props.location.search.indexOf('=');
        let contractAddress = this.props.location.search.slice(index+1);

        //
        messageWay(window.t('bridgePage.history.message1'), 'loading');

        let chainID = await sendWeb3.eth.getChainId();
        let blockHash = await sendWeb3.eth.getBlock(0);
        if(chainID !=  publicChainArr[0].chainID && publicChainArr[0].hash != blockHash.hash){
            this.props.history.goBack();
            let styles = {
                marginTop: '28vh',
            }
            message.loading({
                content: window.t('bridgePage.history.message1'),
                key:'id_1',
                duration: 1,
                style: styles
            });
            return;
        }

        contractToken = new callWeb3.eth.Contract(BridgeCenterContract.abi, MAIN_ADDRESS);
        let BlockNumber = await callWeb3.eth.getBlockNumber();
        // await this.getAddress();

        //
        this.isGetChain(BlockNumber);
    };

    isGetChain = (Numbers:number)=>{
        this.setState({
            isDisetMore:true
        })
        if (publicChainArr[0].index == 1) {
            //
            console.log('Bruned')
            this.getHistory('Bruned',Numbers);
        } else if (publicChainArr[0].index == 2) {
            // 
            console.log('Minted')
            this.getHistory('Minted',Numbers);
        }
    }

    getHistory = async (event:string,Numbers:number) => {
        let NumbersWao = (Numbers - 5000) < 1?0:Numbers - 5000;

        if(!this.state.storeAddress){    
            messageWay(window.t('bridgePage.history.text12'),'error');
            return;
        }
   
        contractToken.getPastEvents(event, {
            filter: {
                receipt: this.state.storeAddress
            },
            fromBlock: 0
        }).then((events:any)=>{
            //HO
            console.log(events)
            if(events.length > 0){
                this.recordPushWay(events.reverse());
            }else{
                messageWay(window.t('bridgePage.history.message3'),'success');
            }
        })
        // 
    };

    recordPushWay = (events:any) => {
        let chainArr:any[] = [];
        let getSeinList:any = getSessionStorage('moneyList');
        if(!getSeinList) return;
        let moneyList = JSON.parse(getSeinList);
        events.forEach((item:any) => {
            // console.log(this.dateTimeWay(parseInt(item.returnValues.time) * 1000))
            moneyList.forEach((chain:any)=>{
                let chiress = item.returnValues.toChainTokenAddress?item.returnValues.toChainTokenAddress:item.returnValues.anchorToken
                if(chain.contractAddress == chiress){
                    chainArr.push({
                        time:this.dateTimeWay(parseInt(item.returnValues.time) * 1000),
                        amount:sendWeb3.utils.fromWei(item.returnValues.amount),
                        sender:item.returnValues.sender,
                        img1:publicChainArr[0].img,
                        text1:publicChainArr[0].text,
                        img2:publicChainArr[1].img,
                        text2:publicChainArr[1].text,
                        symbol:chain.symbol
                    });
                }
            })
        });
        this.setState({
            isDisetMore:false
        })
        messageWay(window.t('bridgePage.history.text11'),'success')

        this.setState({
            detailList: chainArr
        });
    };

    goback = () => {
        this.props.history.goBack(1);
    };

    dateTimeWay(val:any) {
        let date = new Date(val);
        let y = date.getFullYear();
        let m = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
        let d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        let h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        let md = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        return y + '-' + m + '-' + d + ' ' + h + ':' + md;
    }

    render() {
        const { detailList,storeisMobile } = this.state;
        return (
            <div className="detail">
                <div className="detail_title">
                    <span onClick={this.goback}>{window.t('bridgePage.title')}</span> /{' '}
                    <span>{window.t('bridgePage.history.text1')}</span>
                </div>
                {detailList.length > 0 ? (
                    detailList.map((item:any, index:number) => {
                        return (
                            <div className="pos_statistics" key={index}>
                                <Row className="statistics_list">
                                    <Col span={12} className="list_view">
                                        <div>
                                            <div>{item.amount}</div> <span>{item.symbol}</span>
                                        </div>
                                    </Col>
                                    <Col span={12} className="list_view complete">
                                        <button className="btn gray">
                                            {window.t('bridgePage.history.text2')}
                                        </button>
                                        {/* <button className="btn red"></button> */}
                                    </Col>
                                </Row>
                                <Row className="statistics_desc">
                                    <Col span={storeisMobile ? 12 : 6} className="desc_p">
                                        {window.t('bridgePage.history.text3')}
                                    </Col>
                                    <Col span={storeisMobile ? 12 : 18} className="desc_num">
                                        <img src={item.img1} />
                                        {item.text1}
                                    </Col>
                                </Row>
                                <Row className="statistics_desc">
                                    <Col span={storeisMobile ? 12 : 6} className="desc_p">
                                        {window.t('bridgePage.history.text4')}
                                    </Col>
                                    <Col span={storeisMobile ? 12 : 18} className="desc_num">
                                        <img src={item.img2} />
                                        {item.text2}
                                    </Col>
                                </Row>
                                <Row className="statistics_desc">
                                    <Col span={storeisMobile ? 12 : 6} className="desc_p">
                                        {window.t('bridgePage.history.text13')}
                                    </Col>
                                    <Col span={storeisMobile ? 12 : 18} className="desc_num">
                                        {item.sender.slice(0,6) +'...'+ item.sender.slice(-4)}
                                    </Col>
                                </Row>
                                <Row className="statistics_desc">
                                    <Col span={storeisMobile ? 12 : 6} className="desc_p">
                                        {window.t('bridgePage.history.text5')}
                                    </Col>
                                    <Col span={storeisMobile ? 12 : 18} className="desc_num">
                                        {item.time}
                                    </Col>
                                </Row>
                            </div>
                        );
                    })
                ) : (''
                    // <div className="pos_statistics"></div>
                )}

                <div className="more_detail">
                    <div style={{marginTop:'20px',color:'#888'}}>{window.t('bridgePage.history.text10')}</div>
                    {/* {
                        this.state.isMore?(<div style={{marginTop:'20px',color:'#888'}}>{window.t('bridgePage.history.text10')}</div>):(
                            <Button onClick={()=>{
                                this.isGetChain(publicBlockNumber,1)
                            }} disabled={this.state.isDisetMore}>{window.t('pos.detail.text6')}</Button>
                        )
                    } */}
                </div>
            </div>
        );
    }
}