import React,{useState,useEffect,useRef } from 'react';
import { ConfigProvider, Layout } from 'antd';
import { BrowserRouter,Route } from "react-router-dom";
import routers from './router/index'
import zhCN from 'antd/lib/locale/zh_CN';
import moment from 'moment';
import 'moment/locale/zh-cn';
import 'antd/dist/antd.css';
import { setSessionStorage,getSessionStorage,sendWeb3 } from './utils/utils';
import Langage from './components/langage/langage';
// reduxsessionStoragewindow.$address,hookssessionStorage
import { store } from './store/index'

import './locales/i18n';
import { useTranslation } from 'react-i18next';
moment.locale('zh-cn');

function App() {
    const { t } = useTranslation();
    window.t = t;

    const [isMobile] = useState(document.documentElement.offsetWidth > 1200);
    // store.dispatch({ type: "INSERTISMOBILE",isMobile:isMobile });
    window.isMobile = isMobile;

    const [address,setAddress] = useState(store.getState().calculate.address);
    // address
    // const addressRef = useRef(address);
    // addressRef.current = address;
    
    // const getSissAddress = getSessionStorage('sissAddress');
    // if(getSissAddress){
    //     store.dispatch({ type: "INCREMENT",address:getSissAddress });
    // }

    if (window.ethereum) {
        window.ethereum.on('accountsChanged', (accounts:string[]) => {
            if (accounts[0]) {
                window.$address = accounts[0];
            }
            window.location.reload();
        });
        window.ethereum.on('networkChanged', (networkChanged:any) => {
            window.location.reload();
        });
    }

    const web3 = async () => {
        if (window.ethereum) {
            window.ethereum.request({method:'eth_requestAccounts'})
            .then((accounts:any) => {
                if(accounts[0]){
                    // setSessionStorage('sissAddress',accounts[0]);
                    // store.dispatch({ type: "INCREMENT",address:accounts[0] });
                    setAddress(accounts[0]);
                    window.$address = accounts[0];
                }
            })
        }
    };
    web3();
    // useEffect(()=>{
        
    // },[address])

    return (
        <ConfigProvider locale={zhCN}>
            <Layout>
                <BrowserRouter>
                    <Langage></Langage>
                    {window.$address?(
                        routers.map((router,index)=>{
                            return (
                                <Route
                                    key={index}
                                    exact
                                    path={router.path}
                                    component = { router.component }
                                ></Route>
                            )
                        })
                        ):('')
                    }
                </BrowserRouter>
            </Layout>
        </ConfigProvider>
    );
}
export default App;
