export const INCREMENT = "INCREMENT"
export const INSERTISMOBILE = "INSERTISMOBILE"

export const incrementAction = {type: INCREMENT, address: ''}
export const initIsMobile = {type: INSERTISMOBILE, isMobile: false}

interface ReduxState{
    address?:String,
    isMobile?:Boolean
}

interface Action{
    type: String,
    address: String
}

const initData = {
    address: ''
}

interface isMobileAction{
    type: String,
    isMobile: Boolean
}

const isMobileData = {
    isMobile: false
}

const calculate = (state:ReduxState = initData,action:Action)=>{
    switch(action.type){
        case INCREMENT:
            return {
                address: action.address
            }
        default:
            return state
    }
}

const isMobileWay = (state:ReduxState = isMobileData,action:isMobileAction)=>{
    switch(action.type){
        case INSERTISMOBILE:
            return {
                isMobile: action.isMobile
            }
        default:
            return state
    }
}

export { calculate,isMobileWay }