import React,{Component} from 'react';
import { Select, Button, Input} from 'antd';
import {
    messageWay,
    supplementWay,
    supportedAssetsOfAdd,
    convertAmount,
    sdkWay,
    sendWeb3,
    setSessionStorage
} from 'utils/utils'
import './Home.scss'

import { Route } from '../../sdk/bin/routes/routes';
import { ERC20Contract } from '../../sdk/bin/config/config';
// import { store } from 'store/index'

const sdk = sdkWay();
const { Option } = Select;
var fromTokens:any, // tokenBlog
    allowance:any, // 
    isERC20Token:any,
    erc20Token:any; // new

interface NuList{
    [key: string]:any
}

export default class Home extends Component<any, any> {
    constructor(props:any) {
        super(props);

        this.state = {
            selectName: -1,
            isConnect: false, //
            toAddress: '', //
            numValue: '', // 
            fromTokens: null, // 
            erc20Token: null, //
            isShade: false, //
            moneyList: [], //
            mainSelect: false, //
            serviceCharge: '0', //
            mainBalance: '0', //
            balance: '0', // 
            btnLoadings: false, ///loading
            btnText: 2, //
            currency: '', //
            storeAddress:window.$address,
            storeisMobile:window.isMobile,
            chainlist: [
                //
                {
                    img: 'assets/images/m-bianBig.png',
                    text: 'Binance Smart Chain Network',
                    name: 'BSC',
                    chainID: 56,
                    hash: '0x0d21840abff46b96c84b2ac9e10e4f5cdaeb5693cb665db62a2f3b02d2d57b5b',
                    Identifier: Route.ChainIdentifier.bnb,
                    index: 1
                },
                {
                    img: 'assets/images/halo@2x.png',
                    text: 'DT Network',
                    name: 'DT',
                    chainID: 3000,
                    hash: '0xac441e74271f92eafb8be0783c17151d342597c042d6635a89db9bd33c7c646e',
                    Identifier: Route.ChainIdentifier.host,
                    index: 2
                }
            ]
        };
    }

    componentDidMount = async () => {
        //
        if(window.ethereum){
            this.init();
        }
    };

    componentWillUnmount = () => {
        this.setState = (state,callback)=>{
          return;
        };
    }

    init = async () => {
        // console.log(store.getState())
        // console.log(this.state.storeAddress,this.state.storeisMobile);
        let chainID = await sendWeb3.eth.getChainId();
        let blockHash = await sendWeb3.eth.getBlock(0);
        let data:any[] = [];
        
        //  
        this.state.chainlist.forEach((item:any, index:number) => {
            if (item.chainID == chainID && item.hash == blockHash.hash) {
                if (index == 1) {
                    data[0] = this.state.chainlist[index];
                    data[1] = this.state.chainlist[0];
                }
            }
        });

        if (data.length > 0) {
            this.setState({
                chainlist: data
            });
        }

        await this.chainIDConnect();
        await this.supportedAssetsOf();
        await this.getBalanceMain();
    };

    //
    chainIDConnect = async () => {
        let chainID = await sendWeb3.eth.getChainId();
        let blockHash = await sendWeb3.eth.getBlock(0);
        console.log(chainID);
        
        if (
            chainID == this.state.chainlist[0].chainID &&
            blockHash.hash == this.state.chainlist[0].hash &&
            this.state.storeAddress
        ) {
            this.setState({ isConnect: true });
        } else {
            this.setState({ isConnect: false });
        }
    };

    // 
    supportedAssetsOf = async () => {
        if (!this.state.isConnect) {
            this.setState({ moneyList: [] });
            return;
        }
        try{
            let supportedAssets = await sdk.supportedAssetsOf(this.state.chainlist[0].Identifier);
            this.setState({
                moneyList: supportedAssetsOfAdd(this.state.chainlist, supportedAssets)
            });
            console.log(this.state.moneyList);
        }catch(e){
            console.log(e)
        }
    };

    approveToken = async (val:string) => {
        //
        // 
        let opreator;
        if (val == 'into') {
            opreator = await sdk.transferIntoOperator(this.state.chainlist[0].Identifier);
        } else if (val == 'leave') {
            opreator = await sdk.transferLeaveOperator();
        }

        // 
        await erc20Token.methods
            .approve(
                opreator,
                sendWeb3.utils.toWei('1000000') //sendWeb3.utils.toWei(sendWeb3.utils.toBN('1000000'))
            )
            .send({
                from: this.state.storeAddress
            })
            .on('transactionHash', (hash:any) => {
                messageWay(window.t('bridgePage.message1'), 'loading');
                this.setState({ btnLoadings: true });
            })
            .on('receipt', (res:any) => {
                messageWay(window.t('bridgePage.message2'), 'success');
                this.setState({ btnLoadings: false });
                this.isApprove();
            })
            .on('error', (err:any) => {
                messageWay(window.t('bridgePage.message3'), 'error');
            }); //,chainId:1
    };

    doTransferInto = async () => {
        // Ho
        if (!this.isMessage()) {
            return;
        }
        //
        if (
            allowance != -1 &&
            (parseFloat(allowance) < parseFloat(this.state.numValue) || parseFloat(allowance) == 0)
        ) {
            await this.approveToken('into');
        }

        try {
            sdk.doTransferInto(
                window.ethereum,
                fromTokens,
                this.state.storeAddress,
                this.state.toAddress,
                sendWeb3.utils.toBN(convertAmount(this.state.numValue, fromTokens.decimals))
            )
                .once('verifyStateChanged', state => {
                    console.log(`verifyStateChanged: ${state.verifyed} verified`);
                })
                .once('verifyStateSuccessed', state => {
                    console.log(`verifyStateSuccessed: ${state.verifyed} verified`);
                    messageWay(window.t('bridgePage.message4'), 'success');
                    this.successUpdate();
                    this.setState({ btnLoadings: false });
                    this.isApprove();
                    this.getBalanceAndAllowance('');
                })
                .once('transactionHash', hash => {
                    messageWay(window.t('bridgePage.message5'), 'loading');
                    this.setState({ btnLoadings: true });
                })
                .once('receipt', receipt => {
                    console.log('receipted: ');
                    console.log(receipt);
                })
                .on('error', reject => {
                    console.log(reject);
                    messageWay(window.t('bridgePage.message6'), 'error');
                    this.successUpdate();
                    this.setState({ btnLoadings: false });
                });
        } catch (e) {
            console.log(e);
        }
    };

    doTransferLeave = async () => {
        // BSC
        if (!this.isMessage()) {
            return;
        }
        //
        if (allowance != -1 && parseFloat(allowance) < parseFloat(this.state.numValue)) {
            await this.approveToken('leave');
        }
        console.log(
            window.ethereum,
            fromTokens,
            this.state.storeAddress,
            this.state.chainlist[1].Identifier,
            this.state.toAddress,
            sendWeb3.utils.toBN(convertAmount(this.state.numValue, fromTokens.decimals))
        )

        try {
            sdk.doTransferLeave(
                window.ethereum,
                fromTokens,
                this.state.storeAddress,
                this.state.chainlist[1].Identifier,
                this.state.toAddress,
                sendWeb3.utils.toBN(convertAmount(this.state.numValue, fromTokens.decimals))
            )
                .once('verifyStateChanged', state => {
                    console.log(`verifyStateChanged: ${state.verifyed} verified`);
                })
                .once('verifyStateSuccessed', state => {
                    console.log(`verifyStateSuccessed: ${state.verifyed} verified`);
                    messageWay(window.t('bridgePage.message4'), 'success');
                    this.successUpdate();
                    this.setState({ btnLoadings: false });
                    this.isApprove();
                    this.getBalanceAndAllowance('');
                })
                .once('transactionHash', hash => {
                    messageWay(window.t('bridgePage.message5'), 'loading');
                    this.setState({ btnLoadings: true });
                })
                .once('receipt', receipt => {
                    console.log('receipted: ');
                    console.log(receipt);
                })
                .on('error', reject => {
                    console.log(reject);
                    messageWay(window.t('bridgePage.message6'), 'error');
                    console.log('');
                    this.successUpdate();
                    this.setState({ btnLoadings: false });
                });
        } catch (e) {
            console.log(e);
        }
    };

    selectChange = async (val:number) => {
        let textVal = '';
        this.state.moneyList.forEach((item:any, index:number) => {
            if (val == index) {
                fromTokens = item;
                textVal = item.symbol;
                erc20Token = new sendWeb3.eth.Contract(ERC20Contract.abi, item.contractAddress); //
            }
        });

        this.setState({
            selectName: val
        });
        this.successUpdate();
        this.getBalanceAndAllowance(textVal);
    };

    getBalanceAndAllowance = async (textVal:string) => {
        //
        isERC20Token = await sdk.isERC20Token(fromTokens); //true false

        if (isERC20Token) {
            //e
            let opreator;
            if (this.state.chainlist[0].index == 1) {
                console.log('into');
                opreator = await sdk.transferIntoOperator(this.state.chainlist[0].Identifier);
            } else if (this.state.chainlist[0].index == 2) {
                console.log('leave');
                opreator = await sdk.transferLeaveOperator();
            }

            allowance = await erc20Token.methods.allowance(this.state.storeAddress, opreator).call();
            let balance = await erc20Token.methods
                .balanceOf(this.state.storeAddress)
                .call({ from: this.state.storeAddress });
            this.setState({
                balance: supplementWay(sendWeb3.utils.fromWei(balance)),
                currency: textVal,
                mainSelect: false
            });

            this.isApprove();
        } else {
            this.setState({
                balance: supplementWay(this.state.mainBalance),
                btnText: 2,
                currency: textVal,
                mainSelect: true
            });

            allowance = -1;
        }
        console.log('' + allowance);
    };

    getBalanceMain = async () => {
        if(!this.state.storeAddress) return;
        await sendWeb3.eth.getBalance(this.state.storeAddress).then(res => {
            console.log('' + sendWeb3.utils.fromWei(res));
            this.setState({
                mainBalance: sendWeb3.utils.fromWei(res)
            });
        });
    };

    isApprove() {
        //
        this.setState({
            btnText: parseFloat(this.state.numValue) > this.state.balance ? 1 : 2
        });
    }

    conversion = () => {
        if (!this.state.isConnect) {
            messageWay(window.t('bridgePage.message7'), 'error');
            return;
        }
        if (parseFloat(this.state.mainBalance) < 0.0001) {
            messageWay(window.t('bridgePage.message8'), 'error');
            return;
        }

        if (this.state.chainlist[0].index == 1) {
            // Ho
            this.doTransferInto();
        } else if (this.state.chainlist[0].index == 2) {
            // bsc
            this.doTransferLeave();
        }
    };

    successUpdate() {
        this.setState({
            toAddress: '',
            numValue: ''
        });
    }

    isMessage() {
        let isNext = true;
        let state = this.state;
        if (state.selectName == -1) {
            messageWay(window.t('bridgePage.message9'), 'error');
            isNext = false;
        } else if (!state.toAddress) {
            messageWay(window.t('bridgePage.message10'), 'error');
            isNext = false;
        } else if (!sendWeb3.utils.isAddress(state.toAddress)) {
            messageWay(window.t('bridgePage.message11'), 'error');
            return;
        } else if (!state.numValue || parseFloat(state.numValue) < 0.01) {
            messageWay(window.t('bridgePage.message12'), 'error');
            isNext = false;
        } else if (parseFloat(state.balance) < 0.01) {
            messageWay(window.t('bridgePage.message13'), 'error');
            isNext = false;
        }
        return isNext;
    }

    exchange = async () => {
        //
        let list = this.state.chainlist;
        let data = [];
        for (let i = 0; i < list.length; i++) {
            if (i == 0) {
                data[i] = list[1];
            } else if (i == 1) {
                data[i] = list[0];
            }
        }
        this.setState({
            chainlist: data
        });
        await this.chainIDConnect();
        this.supportedAssetsOf();
    };

    HistoryPage = () => {
        // if(!this.state.isConnect){
        //     messageWay(window.t('bridgePage.message7'), 'error');
        //     return;
        // }

        // if (!fromTokens) {
        //     messageWay(window.t('bridgePage.message9'), 'error');
        //     return;
        // }

        setSessionStorage('chainList', JSON.stringify(this.state.chainlist));
        setSessionStorage('moneyList', JSON.stringify(this.state.moneyList));
        // setSessionStorage('fromTokens',JSON.stringify(fromTokens));

        this.props.history.push({
            pathname: '/history'
            // search: `contractAddress=${fromTokens.contractAddress}`
        });
    };


    isShadeWay(val:number) {
        this.setState({
            isShade: true
        });
        setTimeout(() => {
            this.setState({
                isShade: false
            });
        }, val);
    }

    toAddressOnInput(e:any) {
        this.setState({
            toAddress: e.target.value
        });
    }

    handleOnInput(e:any) {
        if (isERC20Token) {
            //false
            this.setState({
                btnText: parseFloat(e.target.value) > allowance ? 1 : 2,
                numValue: e.target.value
            });
        } else {
            this.setState({
                btnText: 2,
                numValue: e.target.value
            });
        }
        this.calculationLeaveFee(e.target.value);
    }

    calculationLeaveFee = async (numValue:any) => {
        if (!fromTokens) {
            return;
        }
        if (this.state.chainlist[0].index == 1) {
            // Ho
            this.setState({
                serviceCharge: 0
            });
        } else if (this.state.chainlist[0].index == 2) {
            // bsc
            this.setState({
                serviceCharge: 0.001
            });
        }
    };

    complete() {
        if (parseFloat(this.state.balance) < 0.01) {
            messageWay(window.t('bridgePage.message13'), 'error');
            return;
        }  

        this.setState({
            btnText: parseFloat(this.state.balance) > allowance ? 1 : 2,
            numValue: this.state.balance
        });
    }

    render() {
        const state = this.state;
        return (
            <div className="page">
                <div className="page-title">
                    {window.t('bridgePage.title')}
                    <div className="page-desc">{window.t('bridgePage.content')}</div>
                </div>
                <div className="page-subject">
                    <div className="title_select">
                        <div className="title">{window.t('bridgePage.text1')}</div>

                        <Select
                            className="selector"
                            allowClear
                            dropdownClassName="dropdownClassName"
                            placeholder={window.t('bridgePage.text2')}
                            onSelect={this.selectChange}
                        >
                            {state.moneyList.map((item:any, index:number) => {
                                return (
                                    <Option className="form-box-view" value={index} key={index}>
                                        <img className="form-box-icon" src={item.img}></img>
                                        <div className="form-box-span">{item.symbol}</div>
                                    </Option>
                                );
                            })}
                        </Select>
                    </div>
                    
                    <div className="transition_view">
                        <div className="trans_start">
                            <div>{window.t('bridgePage.text3')}</div>
                            <div>{window.t('bridgePage.text4')}</div>
                        </div>
                        <div className="trans_deal">
                            <div className="deal_style">
                                <div className="views">
                                    <img src={state.chainlist[0].img} alt="" />
                                    {state.isConnect ? (
                                        <div className="yet"> {window.t('bridgePage.text5')}</div>
                                    ) : (
                                        <div className="no"> {window.t('bridgePage.text6')}</div>
                                    )}
                                </div>
                                <div className="desc">{state.chainlist[0].text}</div>
                            </div>
                            <div className="trans_img" onClick={this.exchange}>
                                <img src={'assets/images/transition_icon.png'} />
                            </div>
                            <div className="deal_style">
                                <div className="views">
                                    <img src={state.chainlist[1].img} alt="" />
                                    {/* <div> </div> */}
                                </div>
                                <div className="desc">{state.chainlist[1].text}</div>
                            </div>
                        </div>
                    </div>
                    <div className="address_view">
                        <div className="name">{window.t('bridgePage.text7')}</div>
                        <Input
                            className="input"
                            value={state.toAddress}
                            onChange={e => {
                                this.toAddressOnInput(e);
                            }}
                        />
                    </div>
                    
                    <div className="shoukuan">
                        <div>{window.t('bridgePage.text8')}</div>
                        <div style={{ fontSize: '12px' }}>
                            {window.t('bridgePage.text9')} {state.balance}{' '}
                            <span style={{ marginLeft: '5px' }}>{state.currency}</span>
                        </div>
                    </div>
                    <div className="amount_Collection">
                        <Input
                            className="Collection"
                            type="number"
                            value={state.numValue}
                            onChange={e => {
                                this.handleOnInput(e);
                            }}
                        />
                        {!state.mainSelect ? (
                            <Button
                            className="allbutton"
                                type="primary"
                                onClick={() => {
                                    this.complete();
                                }}
                            >
                                {window.t('all')}
                            </Button>
                        ) : (
                            ''
                        )}
                    </div>
                    <div className="serviceCharge">
                        {window.t('bridgePage.text10')} {state.serviceCharge}
                    </div>
                    <div className="button_view">
                        <Button
                            type="primary"
                            className="button"
                            onClick={this.conversion}
                            loading={state.btnLoadings}
                        >
                            {state.btnText == 2
                                ? window.t('bridgePage.text11')
                                : window.t('bridgePage.text12')}
                        </Button>
                    </div>
                    
                    <div className="lisi_res" onClick={this.HistoryPage} >{window.t('bridgePage.history.text1')}</div>
                </div>
                {/*  */}
                {/* <div className={state.isShade?'shade':''}></div> */}
            </div>
        );
    }
} 